<template>
  <div :class="['sidebar', { small: sidebarSmall }]" v-show="!isMobile">
    <ul>
      <li><a href="#main" @click="scrollToSection('main')">Top</a></li>
      <li><a href="#background" @click="scrollToSection('background')">程式背景</a></li>
      <li><a href="#portfolio" @click="scrollToSection('portfolio')">作品集</a></li>
      <li><a href="#technology" @click="scrollToSection('technology-connection')">技術連結</a></li>
      <li><a href="#qa" @click="scrollToSection('qa-section')">問與答</a></li>
      <li><a href="#footer" @click="scrollToSection('side-footer')"></a></li>
    </ul>
  </div>
</template>


<script>
export default {
  name: "AppSidebar",
  data() {
    return {
      isMobile: false,
      isScrolling: false, // 是否正在滾動
      sidebarSmall: false, // 側邊欄是否縮小
      scrollTimeout: null, // 滾動停止的計時器
    };
  },
  mounted() {
    this.checkScreenWidth();
    window.addEventListener("resize", this.checkScreenWidth);
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.checkScreenWidth);
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    scrollToSection(sectionId) {
      const element = document.getElementById(sectionId);
      if (element) {
        window.scrollTo({
          top: element.offsetTop,
          behavior: "smooth",
        });
      }
    },
    checkScreenWidth() {
      this.isMobile = window.innerWidth <= 1000;
    },
    handleScroll() {
      // 滾動時設置縮小樣式
      this.sidebarSmall = true;
      this.isScrolling = true;

      // 如果停止滾動，延遲恢復原狀
      clearTimeout(this.scrollTimeout);
      this.scrollTimeout = setTimeout(() => {
        this.isScrolling = false;
        this.sidebarSmall = false;
      }, 270); // 延遲270ms恢復
    },
  },
};

</script>

<style scoped>
.sidebar {
  position: fixed;
  top: 20%;
  left: 20px;
  background-color: rgba(0, 0, 0, 0.8); /* 深色背景 */
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 9999;
  transition: all 0.3s ease; /* 添加過渡效果 */
}

.sidebar.small {
  transform: scale(0.8); /* 縮小效果 */
  opacity: 0.8; /* 減少透明度 */
  padding: 10px; /* 縮小內邊距 */
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar li {
  margin: 12px 0;
}

.sidebar a {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  font-size: 16px;
  display: block;
  padding: 8px 12px;
  border-radius: 4px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.sidebar a:hover {
  color: #000;
  background-color: #ffffff;
}

</style>
